const myDirectives = {}

myDirectives.install = (Vue) => {
  // 拖动某个元素的标题栏时，这个元素进行移动
	Vue.directive('drag', {
		inserted: (el, binding) => {
			let disX, disY;
      el.addEventListener('mousedown', function(e){
				disX = e.clientX - this.parentNode.offsetLeft;
				disY = e.clientY - this.parentNode.offsetTop;
				document.addEventListener('mousemove', moveFn)
				document.addEventListener('mouseup', upFn)
			})
			function moveFn(e){
        el.parentNode.style.left = e.clientX - disX + 'px';
        el.parentNode.style.top = e.clientY - disY + 'px';
			}
			function upFn(){
				document.removeEventListener('mousemove', moveFn);
				document.removeEventListener('mouseup', upFn);
			}
		}
	});
	// 子元素的高度随父元素高度的变化而变化
  Vue.directive('heightAuto', {
    inserted: (el) => {
      document.addEventListener('click', function(){
        // console.log( window.getComputedStyle(el.parentNode, null).height )
        el.style.height = window.getComputedStyle(el.parentNode, null).height;
      })
    }
  });
  /*// 点击关闭按钮，隐藏窗口
  Vue.directive('hideWindow', {
    inserted: (el) => {
      el.addEventListener('click', function(){
        let ele = el.parentNode.parentNode;
        ele.style.opacity = 0;
        setTimeout(() => {
          ele.style.display = none;
        }, 500)
      })
    }
  })*/
}
export default myDirectives



