import Vue from 'vue'
import Router from 'vue-router'

const orderform = r => require.ensure([], () => r(require('@/components/orderform/orderform')), 'orderform')
const orderformAll = r => require.ensure([], () => r(require('@/components/orderform/orderform-all')), 'orderformAll')
const orderformComplete = r => require.ensure([], () => r(require('@/components/orderform/orderform-complete')), 'orderformComplete')
const orderformIncomplete = r => require.ensure([], () => r(require('@/components/orderform/orderform-incomplete')), 'orderformIncomplete')
const distribution = r => require.ensure([], () => r(require('@/components/distribution/distribution')), 'distribution')
const distributionAll = r => require.ensure([], () => r(require('@/components/distribution/distribution-all')), 'distributionAll')
const distributionUndelivered = r => require.ensure([], () => r(require('@/components/distribution/distribution-undelivered')), 'distributionUndelivered')
const distributionDelivered = r => require.ensure([], () => r(require('@/components/distribution/distribution-delivered')), 'distributionDelivered')
const news = r => require.ensure([], () => r(require('@/components/news/news')), 'news')
const newsChat = r => require.ensure([], () => r(require('@/components/news/news-chat')), 'newsChat')
const newsPost = r => require.ensure([], () => r(require('@/components/news/news-post')), 'newsPost')
const img = r => require.ensure([], () => r(require('@/components/img/img')), 'img')
const price = r => require.ensure([], () => r(require('@/components/price/price')), 'price')
const show = r => require.ensure([], () => r(require('@/components/show/show')), 'show')
const seed = r => require.ensure([], () => r(require('@/components/seed/seed')), 'seed')
const update = r => require.ensure([], () => r(require('@/components/update/update')), 'update')
const updateAll = r => require.ensure([], () => r(require('@/components/update/update-all')), 'updateAll')
const updatePublish = r => require.ensure([], () => r(require('@/components/update/update-publish')), 'updatePublish')
const forum = r => require.ensure([], () => r(require('@/components/forum/forum')), 'forum')
const forumAll = r => require.ensure([], () => r(require('@/components/forum/forum-all')), 'forumAll')
const forumPublish = r => require.ensure([], () => r(require('@/components/forum/forum-publish')), 'forumPublish')
const problem = r => require.ensure([], () => r(require('@/components/problem/problem')), 'problem')
const problemAll = r => require.ensure([], () => r(require('@/components/problem/problem-all')), 'problemAll')
const problemPublish = r => require.ensure([], () => r(require('@/components/problem/problem-publish')), 'problemPublish')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: { path: '/orderform/all' }
    },
    /*orderform*/
    {
      path: '/orderform',
      component: orderform,
      children: [
        {
          path: 'complete',
          component: orderformComplete
        },
        {
          path: 'incomplete',
          component: orderformIncomplete
        },
        {
          path: 'all',
          component: orderformAll
        },
        {
          /*不给子路由添加路径属性的值，表示：默认访问/orderform 就会渲染组件all以及orderform*/
          path: '',
          component: orderformAll
        }
      ]
    },
    /*distribution*/
    {
      path: '/distribution',
      component: distribution,
      children: [

        {
          path: 'undelivered',
          component: distributionUndelivered
        },
        {
          path: 'delivered',
          component: distributionDelivered
        },
        {
          path: 'all',
          component: distributionAll
        },
        {
          path: '',
          component: distributionAll
        }
      ]
    },
    /*news*/
    {
      path: '/news',
      component: news,
      children: [
        {
          path: 'chat',
          component: newsChat
        },
        {
          path: 'post',
          component: newsPost
        },
        {
          path: '',
          component: newsChat
        }
      ]
    },
    {
      path: '/img',
      name: 'img',
      component: img
    },
    {
      path: '/price',
      name: 'price',
      component: price
    },
    {
      path: '/show',
      name: 'show',
      component: show
    },
    {
      path: '/seed',
      name: 'seed',
      component: seed
    },
    /* update */
    {
      path: '/update',
      component: update,
      children: [
        {
          path: 'publish',
          component: updatePublish
        },
        {
          path: 'all',
          component: updateAll
        },
        {
          path: '',
          component: updateAll
        }
      ]
    },
    /* forum */
    {
      path: '/forum',
      component: forum,
      children: [
        {
          path: '',
          component: forumAll
        },
        {
          path: 'all',
          component: forumAll
        },
        {
          path: 'publish',
          component: forumPublish
        }
      ]
    },
    /* problem */
    {
      path: '/problem',
      component: problem,
      children: [
        {
          path: '',
          component: problemAll
        },
        {
          path: 'all',
          component: problemAll
        },
        {
          path: 'publish',
          component: problemPublish
        }
      ]
    }
  ]
})



